<template>
  <div class="course_container">
    <!--  -->
    <div class="course_top_contaier">
      <div class="course_top_left"></div>
      <div class="course_top_right">
        <img src="../assets/sousuo.png" class="course_top_right_icon" v-on:click="toSearch" alt />
      </div>
    </div>

  <!--  -->
  <van-dropdown-menu active-color=#eb6100>
  <van-dropdown-item title="" ref="item">
    <div class="prent_box">
      <div class="tm_kind">
        <p>{{this.classifyData[0].name}}</p>
        <div class="tmk_item">
          <span v-for="(item,index) in classifyData[0].child" :key="index" 
          @click="gradeIndex=index" 
          :class="gradeIndex===index?'tmk_item_span_active':'tmk_item_span'">
            {{item.name}}
          </span>
        </div>
      </div>
      <div class="tm_kind">
        <p>{{this.classifyData[1].name}}</p>
        <div class="tmk_item">
          <span v-for="(item,index) in classifyData[1].child" :key="index" 
          @click="subjectIndex=index" 
          :class="subjectIndex===index?'tmk_item_span_active':'tmk_item_span'">
            {{item.name}}
          </span>
        </div>
      </div>
      <div class="search_btn">
        <van-button type="default" style="width:46%;height:2.2rem" @click="ResetData"></van-button>
        <van-button color="#eb6100" style="width:46%;height:2.2rem" @click="onConfirm"></van-button>
      </div>
    </div>
  </van-dropdown-item>
  <!--  -->
  <van-dropdown-item title="" ref="title">
    <div v-for="(item,index) in sortClassify" 
    :key="index" 
    @click="onSortClassify(index)"
    :class="sortClassIndex===index?'sort_classify_item_active':'sort_classify_item'">
        {{item}}
      </div>
  </van-dropdown-item>
  <van-dropdown-item title=""></van-dropdown-item>
</van-dropdown-menu>
<!--  -->
  <!-- name -->
  <van-list
  v-model="loading"
  :finished="finished"
  finished-text=""
  @load="onLoad"
>
  <van-cell>
    <div class="index_item_page" :immediate-check="immediatecheck">
     <div class="ii_item"  v-for="(item,index) in List" :key="index" @click="goCourseDetails(item)">
   <p class="ii_title"><font>{{item.title}}</font></p>
   <!--  -->
   <div class="ii_time">
     <p>{{item.start_play_date | dateFormat}}~{{item.end_play_date |dateFormat}}</p>
     <p>{{item.total_periods}}</p>
   </div>
   <!-- teacher -->
   <div class="ii_teacher" v-for="(ele,index) in item.teachers_list" :key="index">
     <div class="teachder_item">
       <img :src="ele.teacher_avatar" style="width:35%"/>
       <font style="margin:0 0.8rem">{{ele.teacher_name}}</font>
     </div>
   </div>
   <!-- bottom -->
   <div class="ii_info">
     <span>{{item.sales_num}}</span>
     <font :class="item.price==0?'green':'orange'">{{item.price |moneyFilter}}</font>
   </div>
 </div>
 </div>
  </van-cell>
</van-list>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
        classifyData: [
        {
          id: 1,
          name: "",
          parent_id: 0,
          child: [
            {
              id: 1,
              name: ""
            },
            {
              id: 2,
              name: ""
            },
            {
              id: 3,
              name: ""
            },
            {
              id: 4,
              name: ""
            },
            {
              id: 5,
              name: ""
            }
          ]
        },
        {
          id: 2,
          name: "",
          parent_id: 0,
          child: [
            {
              id: 7,
              name: ""
            },
            {
              id: 8,
              name: ""
            },
            {
              id: 9,
              name: ""
            },
            {
              id: 12,
              name: ""
            },
            {
              id: 13,
              name: ""
            }
          ]
        }
      ],
        immediatecheck:false, //=====
        toggle:false, //=====/
        gradeIndex:-1, //=====
        subjectIndex:-1, //=====
        gradeClassify:[], //=====
        subjectClassify:[], //====
        color:false, //====
        List:[], //=====
        loading: false, //
        finished: false, //
        courseInfo:{
          page: 1,  //=====
          limit: 10, //=====  
          course_type: 0, //=====
          classify_id:"", //=====id
          order_by: "", //=====01234
          attr_val_id: "", //=====id
          is_vip: 0 //=====vip 0
        },//=====
        sortClassify:["","","","",""],//
        sortClassIndex:0 //id
    }
  },
  mounted() {
    this.AjaxClassify()
     this.$http.get("/api/app/courseBasis?page=1&limit=10&").then(response => {
      this.List = response.data.data.list;
     console.log(response.data);})
    // this.AjaxcourseBasis()
  },
  methods: {
    // 
    toSearch() {
      this.$router.push("/searchs");
    },
     // 
    goCourseDetails(item) {
      console.log(item);
      this.$http.get(`/api/app/teacher/${item.id}`).then(response => {
        console.log(response.data);
        this.$router.push({
          path: "/courseDetails",
          query: {
            id: item.id,
            data: response.data.data
          }
        });
      });
      // this.$router.push("/courseDetails");
    },
    //
    async AjaxClassify(){
         let {data:response}=await this.$http.get("/api/app/courseClassify")
         console.log(response.data)
        if(response.code !=200){
          this.$toast(response.msg)
          return false
        }
     },
     //
      onConfirm() {
        this.$http
        .get(`/api/app/courseBasis?`, {
          params: this.courseInfo
        })
        .then(response => {
          console.log(response.data);
          this.List = response.data.data.list;
        });
        let Array=[]
        if(this.gradeIndex != -1){
          let gradeID=this.classifyData[0].child[this.gradeIndex].id
          Array.push(gradeID)
        }
        if(this.subjectIndex != -1){
          let subjectID=this.classifyData[1].child[this.subjectIndex].id
          Array.push(subjectID)
        }
        // console.log(Array)
        //Id
        this.courseInfo.attr_val_id=Array.join(",")
        console.log(this.courseInfo.attr_val_id)
        //
      // console.log(this.classifyData[0].child[this.gradeIndex].id)
      // console.log(this.classifyData[1].child[this.subjectIndex].id)
      this.$refs.item.toggle();
    },
    //
    ResetData(){
      this.courseInfo.attr_val_id=""
      this.AjaxcourseBasis()
      this.$refs.item.toggle();
    },
    // 
    onSortClassify(index){
      this.sortClassIndex=index
      // this.courseInfo.order_by=0
      // this.AjaxcourseBasis()
    },
    //
    onLoad() {
      // console.log(12222)
      // 
      // setTimeout  ajax 
      setTimeout(() => {
        this.$http.get(`/api/app/courseBasis?page=${++this.courseInfo.page}&limit=10&`).then((response)=>{
          console.log(response.data)
          if(response.code !=200){
          this.$toast(response.msg)
          return false
        }
        // 
        if(this.courseInfo.page !=1){
          //
          this.List=this.List.concat(response.data.list)
        }
        this.loading=true
        
        if(response.data.list.length>=0){
        //  this.List=response.data.list
         this.finished=true
        }
        })
        // 
        // 
      }, 3000);
    },  
  },
};
</script>
<style>
/*  */
.sort_classify_item
{
  width: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
}
.sort_classify_item_active
{
  width: 100%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
  color: #eb6100;
}
.green{
  color: green;
}
.orange{
  color: orangered;
}
.course_container {
  width: 100vw;
  height: 100vh;
}
.course_top_contaier {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  border-bottom: 1px solid rgb(228, 228, 228);
  background: white;
}
.course_top_left {
  position: absolute;
  left: 45%;
}
.course_top_right_icon {
  width: 25px;
  height: 25px;
}
.prent_box
{
  padding-left: 1rem;
  padding-bottom: 0.5rem;
}
.tm_kind
{
  width: 100%;
  /* height: 9.5rem; */
  display: inline-flex;
  justify-content: center;
  /* align-items: center; */
  flex-wrap: wrap;
  padding-bottom: 1rem;
}
.tm_kind p
{
  width: 100%;
  padding: 0.7rem 0;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
}
.tmk_item
{
  width: 100%;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.tmk_item_span{
  display: inline-flex;
  width: 18.5%;
  height: 2.2rem;
  margin: 0 1.3rem 0.4rem 0;
  justify-content: center;
  border-radius: 0.2rem;
  align-items: center;
  background: #f5f5f5;
}
.tmk_item_span_active{
  display: inline-flex;
  width: 18.5%;
  height: 2.2rem;
  margin: 0 1.3rem 0.4rem 0;
  justify-content: center;
  border-radius: 0.2rem;
  align-items: center;
  background:#EBEEFE;
  color: #eb6100;
}
.search_btn
{
  width: 100%;
  /* background: chartreuse; */
  align-items: center;
  justify-content: space-evenly;
  padding: 1.2rem 0;
  display: inline-flex;
  
}
.index_item_page
{ 
  /* width: 100%; */
  padding: 0.5rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: rgb(240, 238, 238);
  flex-wrap: wrap;
}
.ii_item
{
  padding: 0 0.8rem;
  width: 90%;
  background: white;
  margin-bottom: 1rem;
}
.ii_title
{
  width: 100%;
  padding-top: 1rem;
}
.ii_time
{
  width: 100%;
  display: inline-flex;
  color: #666666;
}
/* .ii_time p:nth-child(1){
  flex: 1;
  height: 0.8rem;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 0.3rem;
}
.ii_time p:nth-child(2)
{
  flex: 1;
  border-left: 1px solid grey;
  height: 0.8rem;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
} */
.ii_teacher
{
  width: 100%;
  height: 4rem;
  display: inline-flex;
  align-items: center;
}
.teachder_item
{
  width: 6rem;
  height: 4rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
}
.ii_info
{
  width: 100%;
  height: 3rem;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
}
</style>